require('normalize.css/normalize.css');
require('styles/App.css');

// Bootstrap
require('bootstrap/dist/js/bootstrap');
require('bootstrap/dist/css/bootstrap.css');

let logoImage = require('./images/logo.png');

import React from 'react';
import ReactDOM from 'react-dom';

class Login extends React.Component {
	render() {
		return (
			<div className="login container">
				<div className="loginlogo">
				</div>
				<form role="form">
				  <div className="form-group">
				    <input type="text" className="form-control" placeholder="请输入您的用户名" />
				  </div>
				  <div className="form-group">
				    <input type="password" className="form-control" placeholder="请输入您的密码" />
				  </div>
				  <div className="form-group">
				  	<button type="submit" className="btn btn-success">登录</button>
				  </div>
				</form>
			</div>
		);
	}
}

ReactDOM.render((<Login />), document.getElementById('app'));


